import React from "react";
import "./Course.scss";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
export default function Course({ courses, name, store, back = "/kythuat/cs6" }) {
     const listSeen = document.querySelectorAll(".course__number--item");
     const [seen, setSeen] = useState(() => {
          const seenID = JSON.parse(window.localStorage.getItem(store));
          return seenID || 0;
     });
     const [per, setPer] = useState(0);
     useEffect(() => {
          const header = document.querySelector(".header");
          const headerH = document.querySelector(".header__h1");
          const headerC = document.querySelector(".header__course");
          const headerB = document.querySelector(".header__back");
          const logo = document.querySelector(".header__fixlogo.none");
          const mam = document.querySelector(".header__mam.none");
          logo && logo.classList.remove("none");
          mam && mam.classList.remove("none");
          headerB.classList.add("none");
          headerH.classList.add("none");
          header.classList.add("black");
          headerC.innerText = name;

          return () => {
               headerB.classList.remove("none");
               header.classList.remove("black");
               const logo = document.querySelector(".header__fixlogo");
               const mam = document.querySelector(".header__mam");
               logo && logo.classList.add("none");
               mam && mam.classList.add("none");
          };
     });
     useEffect(() => {
          const listSeen = document.querySelectorAll(".course__number--item");
          const study = document.querySelector(".course__title i");
          var percents = ~~((seen / (listSeen.length - 1)) * 100);
          study && (study.style.width = `${percents}%`);
          setPer(percents);
     }, [seen]);

     useEffect(() => {
          const listSeen = document.querySelectorAll(".course__number--item");
          listSeen[seen].classList.add("active1");
          for (let i = 0; i < seen + 1; i++) {
               const listSeen = document.querySelectorAll(".course__number--item");
               listSeen[i].classList.add("active");
               let y = i;
               y++;
               listSeen[y] && listSeen[y].classList.remove("disable");
          }
     }, [seen]);

     useEffect(() => {
          const a = seen;
          const seenID = JSON.stringify(a);
          window.localStorage.setItem(store, seenID);
     }, [seen, store]);
     const [youtube, setYoutube] = useState(courses[seen].linkY);
     const handleCourse = (e, course, index) => {
          const listItemActive = document.querySelector(
               ".course__number--item.active1",
          );
          listItemActive && listItemActive.classList.remove("active1");
          e.target.classList.add("active");
          e.target.classList.add("active1");
          setYoutube(course.linkY);

          const listSeen = document.querySelectorAll(".course__number--item");
          listSeen[index + 1] && listSeen[index + 1].classList.remove("disable");
          if (index > seen) {
               setSeen(index);
          }
     };

     return (
          <div className="course__container">
               <ul className="course__number">
                    <li className="course__title">
                         <i></i>
                         <Link
                              to={back}
                              title="Tr v"
                              className="far fa-caret-square-left course__back"
                         ></Link>
                         <span className="course__name">{name}</span>
                         <span className="course__percents">
                              <b className="fas fa-medal"></b> {"   "}
                              Hon thnh{" "}
                              <strong>
                                   {seen + 1}/{listSeen.length}
                              </strong>{" "}
                              bi hc (<strong>{per}%</strong>)
                         </span>
                    </li>
                    <ul className="course__scoll">
                         {courses.map((course, index) => (
                              <li
                                   key={index}
                                   onClick={(e) => handleCourse(e, course, index)}
                                   className="course__number--item disable"
                              >
                                   <span className="course__number--item-title">
                                        {course.title}{" "}
                                        {/* <div className="course__duration">
                                             <i className="fas fa-play-circle"></i>
                                             {"  "}
                                             {course.duration}
                                        </div> */}
                                   </span>
                                   <span className="course__icon">
                                        <i className="fas fa-check"></i>
                                   </span>
                                   <span className="course__lock">
                                        <i className="fas fa-lock"></i>
                                   </span>
                              </li>
                         ))}
                    </ul>
               </ul>

               <div className="course__youtube">
                    <iframe
                         id="movie_player"
                         src={`https://www.youtube.com/embed/${youtube}?autoplay=1`}
                         title="YouTube video player"
                         frameBorder="0"
                         allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                         allowFullScreen="1"
                         className="iframe"
                    ></iframe>
               </div>
          </div>
     );
}
