import React from "react";
// import "./Camera360.scss";
import { Link } from "react-router-dom";
import { useEffect } from "react";
export default function Tips({ CourseList }) {
     let courseRender = CourseList.courseTips;
     useEffect(() => {
          const PSTIP = JSON.parse(window.localStorage.getItem("PSTIP"));
          let W0 = ~~((PSTIP * 100) / (CourseList.courseYoutubePSTIP.length - 1)); // cv
          const PSTT = JSON.parse(window.localStorage.getItem("PSTT"));
          let W1 = ~~((PSTT * 100) / (CourseList.courseYoutubePSTT.length - 1)); // cv
          let MyW = [W0, W1];
          console.log(MyW);
          MyW.forEach((item, i) => {
               const spanWithd = document.querySelectorAll(".cs__img span");
               spanWithd[i].style.width = `${item}%`;
               if (item < 100) {
                    const b = document.querySelectorAll(".cs__img b");
                    b[i].innerText = `${item}%`;
               } else {
                    const b = document.querySelectorAll(".cs__img b");
                    b[i].innerText = " hon thnh";
               }
          });
     });
     return (
          <div className="cs__full">
               <p className="cs__start">
                    <i className="fas fa-camera-retro"></i>
                    <span>Th thut hay cho Designer </span>
               </p>
               {courseRender.map((course, index) => (
                    <div className="cs__container" key={index}>
                         <Link to={course.to} className="cs__img">
                              <img src={course.srcImg} alt="li nh" />
                              <span></span>
                              <b>0%</b>
                         </Link>
                         <p className="cs__content">{course.name}</p>

                         {/*
                              // href="https://www.youtube.com/watch?v=C6m_QYL-Lt0&list=PLH88ngaKjRaQdJkYizDyCE_5Bpcfk9DEO"
                              // target="_blank"
                              // rel="noReferrer"
                              className="cs__btn"
                         >
                              <i className="fas fa-pen-alt"></i>
                              <span>Hc ngay</span>
                         </Link> */}
                    </div>
               ))}
          </div>
     );
}
