import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import EventName from "../components/EventName/EventName";
import NotFound from "../components/NotFound/NotFound";
import EventImages from "./EventImgComponents/Event1";

export default function ImgContain({ IMG_LIST }) {
     const num = 27;
     const allImgList = [
          ...IMG_LIST.Tetyeuthuong,
          ...IMG_LIST.tettrungthu,
          ...IMG_LIST.noel,
          ...IMG_LIST.MemoryImg,
          ...IMG_LIST.prom,
          ...IMG_LIST.tetthieunhi,
          ...IMG_LIST.aoam,
     ];
     return (
          <Router>
               <EventName />
               <Switch>
                    <Route exact path="/Images">
                         <EventImages
                              title="HNH NH K NIM"
                              renderImg={allImgList}
                              num={num}
                         />
                    </Route>
                    <Route exact path="/Images/tetyeuthuong">
                         <EventImages
                              title="TT YU THNG"
                              renderImg={IMG_LIST.Tetyeuthuong}
                         />
                    </Route>
                    <Route exact path="/Images/tuyentnv">
                         <EventImages
                              title="TUYN TNH NGUYN VIN"
                              renderImg={IMG_LIST.tuyentnv}
                         />
                    </Route>
                    <Route exact path="/Images/prom">
                         <EventImages title="PROM NGIHT" renderImg={IMG_LIST.prom} />
                    </Route>
                    <Route exact path="/Images/noel">
                         <EventImages
                              title="GING SINH M P"
                              renderImg={IMG_LIST.noel}
                         />
                    </Route>
                    <Route exact path="/Images/tetthieunhi">
                         <EventImages
                              title="TT THIU NHI"
                              renderImg={IMG_LIST.tetthieunhi}
                         />
                    </Route>
                    <Route exact path="/Images/tettrungthu">
                         <EventImages
                              title="TT TRUNG THU"
                              renderImg={IMG_LIST.tettrungthu}
                         />
                    </Route>
                    <Route exact path="/Images/aoam">
                         <EventImages
                              title="O M MA NG"
                              renderImg={IMG_LIST.aoam}
                         />
                    </Route>
                    <Route exact path="/Images/teambuilding">
                         <EventImages
                              title="TEAM BUILDING"
                              renderImg={IMG_LIST.teambuilding}
                         />
                    </Route>
                    <Route exact path="/Images/chuyengiao">
                         <EventImages
                              title="L CHUYN GIAO"
                              renderImg={IMG_LIST.chuyengiao}
                         />
                    </Route>
                    <Route exact path="/Images/sinhnhat">
                         <EventImages
                              title="SINH NHT THNG"
                              renderImg={IMG_LIST.sinhnhat}
                         />
                    </Route>
                    <Route exact path="/Images/langthieunien">
                         <EventImages
                              title="NHM DY LNG TH C"
                              renderImg={IMG_LIST.langthieunien}
                         />
                    </Route>
                    <Route exact path="/Images/binhtrieu">
                         <EventImages
                              title="NHM DY BNH TRIU"
                              renderImg={IMG_LIST.binhtrieu}
                         />
                    </Route>
                    <Route exact path="*">
                         <NotFound />
                    </Route>
               </Switch>
          </Router>
     );
}
